'use client'
import { useEffect } from "react";

const SportGroup = ({ open, onClose }: any) => {
    if (!open) return null;

    const sportGroups = [
        { name: "Pickleball", image: "/assets/pickleball.webp" },
        { name: "Bóng chuyền", image: "/assets/image 14.webp" },
        { name: "Bóng bàn", image: "/assets/image 13.webp" },
        { name: "Billiards", image: "/assets/image 3.webp" },
        { name: "Cầu lông", image: "/assets/image 4.webp" },
        { name: "Bóng đá", image: "/assets/image 5.webp" },
        { name: "Bóng rổ", image: "/assets/image 6.webp" },
        { name: "Võ thuật", image: "/assets/image 7.webp" },
        { name: "Điền kinh", image: "/assets/image 8.webp" },
        { name: "Bơi lội", image: "/assets/image 9.webp" },
        { name: "e-Sports", image: "/assets/image 10.webp" },
        { name: "Tennis", image: "/assets/image 11.webp" },
    ];

    const divideSportGroup = (list: any) =>
        list.reduce((acc: any, _: any, i: any) =>
            i % 2 === 0 ? [...acc, list.slice(i, i + 2)] : acc, []);

    // Tắt bằng phím ESC
    useEffect(() => {
        const handleEsc = (e: any) => e.key === "Escape" && onClose();
        window.addEventListener("keydown", handleEsc);
        return () => window.removeEventListener("keydown", handleEsc);
    }, []);

    return (
        <div className="sg-modal">
            {/* Overlay */}
            <div className="sg-overlay" onClick={onClose}></div>

            {/* Modal content */}
            <div className="sg-content">
                <div className="sport-group__container">
                    <div className="sport-group__title-container">
                        <div className="sport-group__title">Nhóm thể thao</div>

                        {/* Icon đóng */}
                        <div className="sport-group__icon" onClick={onClose}>
                            <svg width="32" height="32" viewBox="0 0 32 32">
                                <rect width="32" height="32" rx="16" fill="#F6F6F6" />
                                <path d="M19.7 20.77L11.23 12.29..." fill="#121212" />
                                <path d="M11.23 20.77L19.71 12.29..." fill="#121212" />
                            </svg>
                        </div>
                    </div>

                    <div className="sport-group__body">
                        {divideSportGroup(sportGroups).map((item: any, index: any) => (
                            <div key={index} className="sport-group__row">
                                <div className="sport-group__item">
                                    <img src={item[0].image} className="image" />
                                    {item[0].name}
                                </div>
                                <div className="sport-group__item">
                                    <img src={item[1].image} className="image" />
                                    {item[1].name}
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        </div>
    );
};

export default SportGroup;
